import React from 'react';

import classes from './Navigation.module.css';



const Navigation = () => {
  const ctx = {
    isLoggedIn:true
  };

  
  
  return (<nav className={classes.nav}>
    <ul>
      {ctx.isLoggedIn && (
        <li>
          <a href="/">Home</a>
        </li>
      )}
      {ctx.isLoggedIn && (
        <li>
          <a href="/">Resume</a>
        </li>
      )}
      {ctx.isLoggedIn && (
        <li>
          <button onClick={ctx.onClick}>Go</button>
        </li>
      )}
    </ul>
  </nav>);
      
    
  
};

export default Navigation;
